.labels {
    display:flex;
    flex-direction: column;
    text-align: right;
    margin-right: 20px;
}

.inputs {
    position: relative;
    display:flex;
    flex-direction: column;
    margin-right: 100px; 
}
input:focus {
    outline:none;
}
input {
    text-indent: 10px; 
    font-size: 24px;
    border:none; 
    border-radius: 25px;
    width: 200px;
    height: 35px; 
}

.form {
    display: flex; 
}