
div {
  font-family: Arial;
  font-weight: 100;
  text-align: center; 
  font-size: 28px; 
  width: 100vw;
}

*, *::before, *::after {
  box-sizing: inherit;
}

html {
  /* this makes sure the padding and the border is included in the box sizing */
  box-sizing: border-box;
  overflow-x: hidden;
}

html body {
  padding: 0;
  margin: 0;
  overflow-x: inherit;
}

.big-container {
  background-color: lightblue;
  min-width: 100vw;
  min-height: 100vh;
}

.header {
  background-color:lightslategray;
  text-align: left; 
  width: 100%; 
  position:relative;
  bottom: 40px; 
  color: white; 
  height: 120px; 
}

li {
  list-style: none;
}
.error {
  text-align: center; 
  color: lightcoral;
  width: 40%; 
  font-weight: 100;
  font-size: 28px;
  margin-bottom: 10px;
}
.success {
  text-align: center; 
  color: lightgreen;
  width: 40%; 
  font-weight: 0;
  font-size: 28px;
  margin-bottom: 10px;
}
